import axios from "axios";
import { types } from "../types/types";
import { startLogout } from "./auth";

const url = "v1/api";

export const startNewClient = async (name, email, phone, address) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "application/json",
      authorization: userInfo.token,
    };
    const formData = {
      name: name,
      email: email,
      phone: phone,
      address: address,
    };
    await axios.post(`${url}/client`, formData, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    console.log(err);
    return {
      status: false,
    };
  }
};

export const addNewClient = (id, client) => ({
  type: types.clientAddNew,
  payload: {
    id,
    ...client,
  },
});

export const refreshClient = (id, client) => ({
  type: types.clientUptaded,
  payload: {
    id,
    client: {
      id,
      ...client,
    },
  },
});

export const startLoadingClient = async (pageNum) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "Application/json",
      authorization: userInfo.token,
    };

    const res = await axios.get(`${url}/client?pageNum=${pageNum}`, {
      headers,
    });

    const client = res.data;

    const clients = [];
    client.forEach((snapHijo) => {
      clients.push({
        id: snapHijo.id,
        ...snapHijo,
      });
    });
    return {
      status: true,
      clients: clients,
    };
  } catch (err) {
    if (err.response) {
      if (err.response.data.message === "Invalid token") {
        startLogout();
        return {
          status: false,
          message: "Inicie sesin nuevamente",
        };
      }
      return {
        status: false,
      };
    } else {
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const startDeleteClient = async (id) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "Application/json",
      authorization: userInfo.token,
    };
    await axios.delete(`${url}/client/${id}`, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    if (err.response) {
      if (err.response.data.message === "Invalid token") {
        startLogout();
        return {
          status: false,
          message: "Inicie sesin nuevamente",
        };
      }
      if (
        err.response.data.message ===
        'update or delete on table "Client" violates foreign key constraint "Quotation_clientId_fkey" on table "Quotation"'
      ) {
        return {
          status: false,
          message: "No puede eliminar el cliente, ya que tiene cotizaciones.",
        };
      }
      return {
        status: false,
        message: "Ocurrio un problema, intente mas tarde.",
      };
    } else {
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const startUpdateClient = async (id, name, email, phone, address) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "application/json",
      authorization: userInfo.token,
    };

    const formData = {
      name: name,
      email: email,
      phone: phone,
      address: address,
    };

    await axios.patch(`${url}/client/${id}`, formData, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    if (err.response.data.message === "Invalid token") {
      startLogout();
      return {
        status: false,
        message: "Inicie sesin nuevamente",
      };
    } else {
      console.log(err.request);
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const deleteClient = (id) => ({
  type: types.clientDeleted,
  payload: id,
});

export const setClient = (client) => ({
  type: types.clientLoad,
  payload: client,
});

export const clientLogout = () => ({
  type: types.clientLogoutCleaning,
});
