import { types } from "../types/types";

const initialState = {
  quotations: [],
  active: null,
};

export const quotationReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.quotationAddNew:
      return {
        ...state,
        quotations: [action.payload, ...state.quotations],
      };

    case types.quotationLoad:
      return {
        ...state,
        quotations: [...action.payload],
      };

    case types.quotationUpdated:
      return {
        ...state,
        quotations: state.quotations.map((quotation) =>
          quotation.id === action.payload.id
            ? action.payload.quotation
            : quotation
        ),
      };

    case types.quotationDelete:
      return {
        ...state,
        active: null,
        quotations: state.quotations.filter(
          (quotation) => quotation.id !== action.payload
        ),
      };

    case types.quotationLogoutCleaning:
      return {
        ...state,
        active: null,
        quotations: [],
      };

    default:
      return state;
  }
};
