import React, { useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import ModalService from "../Modal/ModalService";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";
import ModalEditService from "../Modal/ModalEditService";
import {
  deleteService,
  setService,
  startDeleteService,
  startLoadingService,
} from "../../actions/service";
import Loading from "../Loader/Loading";

export default function TableService() {
  const dispatch = useDispatch();
  const [checking, setChecking] = useState(false);
  const { services } = useSelector((state) => state.service);
  const [showModal, setShowModal] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [dataService, setDataService] = useState({});

  const [currentPage, setCurrentPage] = useState(1);
  const [search, setSearch] = useState("");

  const ITEMS_PER_PAGE = 5;

  const handleInputChanges = (e) => {
    setSearch(e.target.value);
  };

  const nextPage = () => {
    setCurrentPage(currentPage + 1);
  };

  const previousPage = () => {
    setCurrentPage(currentPage - 1);
  };

  const closeModal = () => {
    setShowModal(false);
  };

  const isDisabled = () => {
    if (currentPage === 1) {
      return true;
    }
    return false;
  };

  const handleDeleteService = (id) => {
    confirmAlert({
      title: "Confirmar para eliminar",
      message: "Estas Seguro de Eliminar el Servicio?",
      buttons: [
        {
          label: "Si",
          onClick: async () => {
            await deleteServiceFront(id);
          },
        },
        {
          label: "No",
        },
      ],
    });
  };

  const deleteServiceFront = async (id) => {
    setChecking(true);
    const data = await startDeleteService(id);
    if (data.status) {
      dispatch(deleteService(id));
      const data = await startLoadingService(1);
      if (data.status) {
        dispatch(setService(data.services));
      }
    }
    setChecking(false);
  };

  const commentsData = useMemo(() => {
    let computedServices = services;

    if (search) {
      computedServices = computedServices.filter(
        (service) =>
          service.name.toLowerCase().includes(search.toLowerCase()) ||
          service.price.includes(search)
      );
    }

    //Current Page slice 0,
    return computedServices.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [services, currentPage, search]);

  if (checking) {
    return <Loading />;
  }

  return (
    <div className="antialiased font-sans bg-gray-200">
      <div className="container mx-auto px-4 sm:px-8">
        <div className="py-8">
          <div>
            <h2 className="text-2xl font-semibold leading-tight">Servicios</h2>
          </div>
          <br />
          <div className="p-5">
            <div className="flex justify-around items-baseline flex-wrap">
              <div className="flex m-2">
                <div className="relative text-gray-600">
                  <input
                    type="search"
                    id="search"
                    name="search"
                    placeholder="Busqueda"
                    className="bg-white h-10 px-5 pr-10 rounded-full text-sm focus:outline-none"
                    value={search}
                    onChange={handleInputChanges}
                  />
                  <button type="submit" className="absolute top-0 mt-3 mr-4">
                    <svg
                      className="h-4 w-4 fill-current"
                      xmlns="http://www.w3.org/2000/svg"
                      version="1.1"
                      id="Capa_1"
                      x="0px"
                      y="0px"
                      viewBox="0 0 56.966 56.966"
                      width="512px"
                      height="512px"
                    >
                      <path d="M55.146,51.887L41.588,37.786c3.486-4.144,5.396-9.358,5.396-14.786c0-12.682-10.318-23-23-23s-23,10.318-23,23  s10.318,23,23,23c4.761,0,9.298-1.436,13.177-4.162l13.661,14.208c0.571,0.593,1.339,0.92,2.162,0.92  c0.779,0,1.518-0.297,2.079-0.837C56.255,54.982,56.293,53.08,55.146,51.887z M23.984,6c9.374,0,17,7.626,17,17s-7.626,17-17,17  s-17-7.626-17-17S14.61,6,23.984,6z" />
                    </svg>
                  </button>
                </div>
              </div>
              <div className="flex m-2">
                <button
                  className="text-base  rounded-r-none  hover:scale-110 focus:outline-none flex justify-center px-4 py-2 rounded font-bold cursor-pointer 
        hover:bg-teal-700 hover:text-teal-100 
        bg-teal-100 
        text-teal-700 
        border duration-200 ease-in-out 
        border-teal-600 transition"
                  onClick={() => {
                    setShowModal(true);
                    setIsEdit(false);
                  }}
                >
                  <div className="flex leading-5">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="100%"
                      height="100%"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-save w-5 h-5 mr-1"
                    >
                      <path d="M19 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h11l5 5v11a2 2 0 0 1-2 2z"></path>
                      <polyline points="17 21 17 13 7 13 7 21"></polyline>
                      <polyline points="7 3 7 8 15 8"></polyline>
                    </svg>
                    Agregar
                  </div>
                </button>
              </div>
            </div>
          </div>

          <div className="-mx-4 px-4 sm:px-8 py-4 overflow-x-auto">
            <div className="inline-block min-w-full shadow rounded-lg overflow-hidden">
              <table className="min-w-full leading-normal">
                <thead>
                  <tr>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Nombre
                    </th>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Precio
                    </th>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Acciones
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {commentsData.map((service) => (
                    <tr key={service.id}>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                        <div className="flex items-center">
                          <div className="ml-3">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {service.name}
                            </p>
                          </div>
                        </div>
                      </td>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                        <p className="text-gray-900 whitespace-no-wrap">
                          {service.price}
                        </p>
                      </td>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm text-left">
                        <div className="flex">
                          <div className="w-4 mr-2 transform text-blue-900 hover:text-blue-500 hover:scale-110 cursor-pointer">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                              onClick={() => {
                                setDataService(service);
                                setShowModal(true);
                                setIsEdit(true);
                              }}
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth="2"
                                d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                              />
                            </svg>
                          </div>
                          <div className="w-4 mr-2 transform text-red-600 hover:text-red-900 hover:scale-110 cursor-pointer">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                              onClick={() => handleDeleteService(service.id)}
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth="2"
                                d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                              />
                            </svg>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <div className="px-5 py-5 bg-white border-t flex flex-col xs:flex-row items-center xs:justify-between          ">
                <div className="inline-flex mt-2 xs:mt-0">
                  <button
                    onClick={previousPage}
                    disabled={isDisabled()}
                    className={
                      isDisabled()
                        ? "text-sm bg-gray-300 hover:bg-red-400 text-gray-800 font-semibold py-2 px-4 rounded-l"
                        : "text-sm bg-blue-300 hover:bg-blue-400 text-gray-800 font-semibold py-2 px-4 rounded-l"
                    }
                  >
                    Anterior
                  </button>
                  <button
                    onClick={nextPage}
                    disabled={commentsData.length < 5}
                    className={
                      commentsData.length < 5
                        ? "text-sm bg-gray-300 hover:bg-red-400 text-gray-800 font-semibold py-2 px-4 rounded-r"
                        : "text-sm bg-blue-300 hover:bg-blue-400 text-gray-800 font-semibold py-2 px-4 rounded-r"
                    }
                  >
                    Siguiente
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {showModal && isEdit && (
        <ModalEditService closeModal={closeModal} service={dataService} />
      )}
      {showModal && !isEdit && <ModalService closeModal={closeModal} />}
    </div>
  );
}
