import axios from "axios";
import { types } from "../types/types";
import { startLogout } from "./auth";

const url = "v1/api";

export const startNewQuotation = async (clientId, serviceList, total) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "application/json",
      authorization: userInfo.token,
    };
    const formData = {
      clientId: clientId,
      serviceList: serviceList,
      total: total,
      status: false,
      date: new Date(),
    };
    await axios.post(`${url}/quotation`, formData, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    console.log(err);
    return {
      status: false,
    };
  }
};

export const sendEmail = async (pdfData, client) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "application/json",
      authorization: userInfo.token,
    };

    var formData = new FormData();
    formData.append("pdfData", pdfData);
    formData.append("client", client);

    await axios.post(`${url}/sendemail`, formData, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    console.log(err.response);
    console.log(err.message);
    return {
      status: false,
    };
  }
};

export const addNewQuotation = (id, quotation) => ({
  type: types.quotationAddNew,
  payload: {
    id,
    ...quotation,
  },
});

export const refreshQuotation = (id, quotation) => ({
  type: types.quotationUptaded,
  payload: {
    id,
    quotation: {
      id,
      ...quotation,
    },
  },
});

export const startLoadingQuotation = async (pageNum) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "Application/json",
      authorization: userInfo.token,
    };

    const res = await axios.get(`${url}/quotation?pageNum=${pageNum}`, {
      headers,
    });

    const quotation = res.data;

    const quotations = [];
    quotation.forEach((snapHijo) => {
      quotations.push({
        id: snapHijo.id,
        ...snapHijo,
      });
    });
    return {
      status: true,
      quotations: quotations,
    };
  } catch (err) {
    if (err.response) {
      if (err.response.data.message === "Invalid token") {
        startLogout();
        return {
          status: false,
          message: "Inicie sesin nuevamente",
        };
      }
      console.log(err);
      return {
        status: false,
      };
    } else {
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const startDeleteQuotation = async (id) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "Application/json",
      authorization: userInfo.token,
    };

    await axios.delete(`${url}/quotation/${id}`, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    if (err.response) {
      if (err.response.data.message === "Invalid token") {
        startLogout();
        return {
          status: false,
          message: "Inicie sesin nuevamente",
        };
      }
    } else {
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const deleteQuotation = (id) => ({
  type: types.quotationDeleted,
  payload: id,
});

export const setquotation = (quotation) => ({
  type: types.quotationLoad,
  payload: quotation,
});

export const quotationLogout = () => ({
  type: types.quotationLogoutCleaning,
});
