import React from "react";
import { useDispatch } from "react-redux";
import { useForm } from "../hooks/useForm";
import { validateEmail } from "../hooks/validation";
import Swal from "sweetalert2";
import { login, startLogin } from "../actions/auth";

export default function Login() {
  const dispatch = useDispatch();

  const [formValues, handleInputChange] = useForm({
    username: "",
    password: "",
  });

  const { username, password } = formValues;

  const handleLogin = async (e) => {
    e.preventDefault();
    if (isFormValid()) {
      const data = await startLogin(username, password);
      if (!data.status) {
        Swal.fire("Error", data.message, "error");
      } else {
        dispatch(login(data.token, data.username));
        window.location.href = "/";
      }
    }
  };

  const isFormValid = () => {
    if (!validateEmail(username)) {
      Swal.fire("Error", "El correo es requerido", "error");
      return false;
    } else if (password.length < 5) {
      Swal.fire("Error", "Contrasea mayor 5 caracteres", "error");
      return false;
    }

    return true;
  };

  return (
    <>
      <div className="container mx-auto mt-20 px-4 h-full">
        <div className="flex content-center items-center justify-center h-full">
          <div className="w-full lg:w-4/12 px-4">
            <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-gray-300 border-0">
              <div className="rounded-t mb-0 px-6 py-6">
                <div className="text-center mb-3">
                  <h6 className="text-gray-600 text-sm font-bold">
                    Ingresar al Sistema
                  </h6>
                </div>
                <hr className="mt-6 border-b-1 border-gray-400" />
              </div>
              <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                <form onSubmit={handleLogin}>
                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-gray-700 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Correo electrnico
                    </label>
                    <input
                      id="username"
                      name="username"
                      type="email"
                      className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                      placeholder="Correo electrnico"
                      value={username}
                      onChange={handleInputChange}
                    />
                  </div>

                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-gray-700 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Contrasea
                    </label>
                    <input
                      id="password"
                      name="password"
                      type="password"
                      className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                      placeholder="Contrasea"
                      value={password}
                      onChange={handleInputChange}
                    />
                  </div>

                  <div className="text-center mt-6">
                    <button
                      className="bg-gray-900 text-white active:bg-gray-700 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full ease-linear transition-all duration-150"
                      type="submit"
                    >
                      Iniciar Sesin
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
