import React, { useState } from "react";
import { useDispatch } from "react-redux";
import Swal from "sweetalert2";
import {
  setService,
  startLoadingService,
  startUpdateService,
} from "../../actions/service";
import { useForm } from "../../hooks/useForm";
import Loading from "../Loader/Loading";

export default function ModalEditService({ closeModal, service }) {
  const dispatch = useDispatch();
  const [checking, setChecking] = useState(false);

  var [formValues, handleInputChange] = useForm({
    id: service.id,
    name: service.name,
    price: service.price,
  });
  var { name, price } = formValues;

  const editservice = async () => {
    setChecking(true);
    if (name === service.name && price === service.price) {
      setChecking(false);
      return false;
    }
    if (name.trim().length === 0 || price <= 0) {
      Swal.fire("Error", "Ingresar todos los campos", "error");
      setChecking(false);
      return false;
    }
    await startUpdateService(service.id, name, price);
    const data = await startLoadingService(1);
    if (data.status) {
      dispatch(setService(data.services));
    }
    setChecking(false);
    closeModal(false);
  };

  if (checking) {
    return <Loading />;
  }

  return (
    <div
      className="fixed z-10 inset-0 overflow-y-auto"
      aria-labelledby="modal-title"
      role="dialog"
      aria-modal="true"
    >
      <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div
          className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
          aria-hidden="true"
        ></div>

        <span
          className="hidden sm:inline-block sm:align-middle sm:h-screen"
          aria-hidden="true"
        >
          &#8203;
        </span>

        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
          <div className="leading-loose">
            <form className="max-w-xl m-4 p-10 bg-white rounded shadow-xl">
              <p className="text-gray-800 font-medium text-center">
                Informacin del Servicio
              </p>
              <div className="mt-5">
                <label className="block text-sm text-gray-600" htmlFor="name">
                  Nombre
                </label>
                <input
                  className="w-full px-5 py-1 text-gray-700 bg-gray-200 rounded"
                  id="name"
                  name="name"
                  type="text"
                  required=""
                  aria-label="Name"
                  value={name}
                  onChange={handleInputChange}
                />
              </div>
              <div className="mt-2">
                <label className="block text-sm text-gray-600" htmlFor="price">
                  Precio
                </label>
                <input
                  className="w-full px-5 py-1 text-gray-700 bg-gray-200 rounded"
                  id="price"
                  name="price"
                  type="number"
                  required=""
                  aria-label="price"
                  value={price}
                  onChange={handleInputChange}
                />
              </div>

              <div className="bg-gray-50 px-4 py-3 mt-5 sm:px-6 sm:flex sm:flex-row-reverse">
                <button
                  type="button"
                  className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-blue-400 text-base font-medium text-white hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
                  onClick={() => editservice()}
                  disabled={name.trim().length === 0 || price <= 0}
                >
                  Editar
                </button>
                <button
                  type="button"
                  className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm"
                  onClick={() => closeModal(false)}
                >
                  Cancelar
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
