import React, { useMemo, useState } from "react";
import { useSelector } from "react-redux";

export default function TableServiceAdd({ closeModal, addService }) {
  const { services } = useSelector((state) => state.service);

  const [currentPage, setCurrentPage] = useState(1);
  const [search, setSearch] = useState("");

  const ITEMS_PER_PAGE = 3;

  const handleInputChanges = (e) => {
    setSearch(e.target.value);
  };

  const nextPage = () => {
    setCurrentPage(currentPage + 1);
  };

  const previousPage = () => {
    setCurrentPage(currentPage - 1);
  };

  const isDisabled = () => {
    if (currentPage === 1) {
      return true;
    }
    return false;
  };

  const commentsData = useMemo(() => {
    let computedServices = services;

    if (search) {
      computedServices = computedServices.filter(
        (service) =>
          service.name.toLowerCase().includes(search.toLowerCase()) ||
          service.price.includes(search)
      );
    }
    return computedServices.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [services, currentPage, search]);

  return (
    <div
      className="fixed z-10 inset-0 overflow-y-auto"
      aria-labelledby="modal-title"
      role="dialog"
      aria-modal="true"
    >
      <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div
          className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
          aria-hidden="true"
        ></div>

        <span
          className="hidden sm:inline-block sm:align-middle sm:h-screen"
          aria-hidden="true"
        >
          &#8203;
        </span>

        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
          <div className="leading-loose">
            <div className="antialiased font-sans bg-gray-200">
              <div className="container mx-auto px-4 sm:px-8">
                <div>
                  <br />
                  <div>
                    <div className="flex justify-around items-baseline flex-wrap">
                      <div className="flex m-2">
                        <div className="relative text-gray-600">
                          <input
                            type="search"
                            id="search"
                            name="search"
                            placeholder="Busqueda"
                            className="bg-white h-10 px-5 pr-10 rounded-full text-sm focus:outline-none"
                            value={search}
                            onChange={handleInputChanges}
                          />
                          <button
                            type="submit"
                            className="absolute top-0 mt-3 mr-4"
                          >
                            <svg
                              className="h-4 w-4 fill-current"
                              xmlns="http://www.w3.org/2000/svg"
                              version="1.1"
                              id="Capa_1"
                              x="0px"
                              y="0px"
                              viewBox="0 0 56.966 56.966"
                              width="512px"
                              height="512px"
                            >
                              <path d="M55.146,51.887L41.588,37.786c3.486-4.144,5.396-9.358,5.396-14.786c0-12.682-10.318-23-23-23s-23,10.318-23,23  s10.318,23,23,23c4.761,0,9.298-1.436,13.177-4.162l13.661,14.208c0.571,0.593,1.339,0.92,2.162,0.92  c0.779,0,1.518-0.297,2.079-0.837C56.255,54.982,56.293,53.08,55.146,51.887z M23.984,6c9.374,0,17,7.626,17,17s-7.626,17-17,17  s-17-7.626-17-17S14.61,6,23.984,6z" />
                            </svg>
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="-mx-4 px-4 sm:px-8 py-4 overflow-x-auto">
                    <div className="inline-block min-w-full shadow rounded-lg overflow-hidden">
                      <table className="min-w-full leading-normal">
                        <thead>
                          <tr>
                            <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                              Nombre
                            </th>
                            <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                              Precio
                            </th>
                            <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                              Accin
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          {commentsData.map((service) => (
                            <tr key={service.id}>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <div className="flex items-center">
                                  <div className="ml-3">
                                    <p className="text-gray-900 whitespace-normal">
                                      {service.name}
                                    </p>
                                  </div>
                                </div>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                                <p className="text-gray-900 whitespace-normal">
                                  ${service.price}
                                </p>
                              </td>
                              <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm text-left">
                                <div className="flex">
                                  <div>
                                    <button
                                      className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
                                      onClick={() => addService(service)}
                                    >
                                      Agregar
                                    </button>
                                  </div>
                                </div>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                      <div className=" bg-white border-t flex flex-col xs:flex-row items-center xs:justify-between">
                        <div className="inline-flex mt-2 xs:mt-0">
                          <button
                            onClick={previousPage}
                            disabled={isDisabled()}
                            className="text-sm bg-gray-300 hover:bg-gray-400 text-gray-800 font-semibold py-2 px-4 rounded-l"
                          >
                            Anterior
                          </button>
                          <button
                            onClick={nextPage}
                            disabled={commentsData.length < 3}
                            className="text-sm bg-gray-300 hover:bg-gray-400 text-gray-800 font-semibold py-2 px-4 rounded-r"
                          >
                            Siguiente
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
              <button
                type="button"
                className="text-center w-full text-white bg-red-600 p-3 duration-300 rounded-sm hover:bg-red-800"
                onClick={() => closeModal(false)}
              >
                Salir
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
