import axios from "axios";
import { types } from "../types/types";
import { startLogout } from "./auth";

const url = "v1/api";

export const startNewService = async (name, price) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "application/json",
      authorization: userInfo.token,
    };
    const formData = {
      name: name,
      price: price,
      time: "1h",
    };
    await axios.post(`${url}/service`, formData, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    console.log(err);
    return {
      status: false,
    };
  }
};

export const addNewService = (id, service) => ({
  type: types.serviceAddNew,
  payload: {
    id,
    ...service,
  },
});

export const refreshService = (id, service) => ({
  type: types.serviceUptaded,
  payload: {
    id,
    service: {
      id,
      ...service,
    },
  },
});

export const startLoadingService = async (pageNum) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "Application/json",
      authorization: userInfo.token,
    };

    const res = await axios.get(`${url}/service?pageNum=${pageNum}`, {
      headers,
    });

    const service = res.data;

    const services = [];
    service.forEach((snapHijo) => {
      services.push({
        id: snapHijo.id,
        ...snapHijo,
      });
    });
    return {
      status: true,
      services: services,
    };
  } catch (err) {
    if (err.response) {
      if (err.response.data.message === "Invalid token") {
        startLogout();
        return {
          status: false,
          message: "Inicie sesin nuevamente",
        };
      }
      return {
        status: false,
      };
    } else {
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

