export const startUpdateService = async (id, name, price) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "application/json",
      authorization: userInfo.token,
    };

    const formData = {
      name: name,
      price: price,
      time: "1h",
    };

    await axios.patch(`${url}/service/${id}`, formData, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    if (err.response.data.message === "Invalid token") {
      startLogout();
      return {
        status: false,
        message: "Inicie sesin nuevamente",
      };
    } else {
      console.log(err.request);
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const deleteService = (id) => ({
  type: types.serviceDeleted,
  payload: id,
});

export const setService = (service) => ({
  type: types.serviceLoad,
  payload: service,
});

export const serviceLogout = () => ({
  type: types.serviceLogoutCleaning,
});
