import React, { useState } from "react";
import { useDispatch } from "react-redux";
import Swal from "sweetalert2";
import {
  setClient,
  startLoadingClient,
  startNewClient,
} from "../../actions/client";
import { useForm } from "../../hooks/useForm";
import Loading from "../Loader/Loading";

export default function Modal({ closeModal }) {
  const dispatch = useDispatch();
  const [checking, setChecking] = useState(false);

  var [formValues, handleInputChange] = useForm({
    name: "",
    email: "",
    phone: "",
    address: "",
  });
  var { name, email, phone, address } = formValues;

  const createClient = async () => {
    setChecking(true);
    if (
      name.trim().length === 0 ||
      email.trim().length === 0 ||
      phone.trim().length === 0 ||
      address.trim().length === 0
    ) {
      return false;
    }
    const data = await startNewClient(name, email, phone, address);
    if (data.status) {
      const data = await startLoadingClient(1);

      if (data.status) {
        dispatch(setClient(data.clients));
      }
      setChecking(false);
      closeModal(false);
    } else {
      setChecking(false);
      Swal.fire("Error", "Error del servidor", "error");
    }
  };

  if (checking) {
    return <Loading />;
  }

  return (
    <div
      className="fixed z-10 inset-0 overflow-y-auto"
      aria-labelledby="modal-title"
      role="dialog"
      aria-modal="true"
    >
      <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
        <div
          className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
          aria-hidden="true"
        ></div>

        <span
          className="hidden sm:inline-block sm:align-middle sm:h-screen"
          aria-hidden="true"
        >
          &#8203;
        </span>

        <div className="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
          <div className="leading-loose">
            <form className="max-w-xl m-4 p-10 bg-white rounded shadow-xl">
              <p className="text-gray-800 font-medium text-center">
                Informacin del Cliente
              </p>
              <div className="mt-5">
                <label className="block text-sm text-gray-600" htmlFor="name">
                  Nombre
                </label>
                <input
                  className="w-full px-5 py-1 text-gray-700 bg-gray-200 rounded"
                  id="name"
                  name="name"
                  type="text"
                  required=""
                  aria-label="Name"
                  value={name}
                  onChange={handleInputChange}
                />
              </div>
              <div className="mt-2">
                <label className="block text-sm text-gray-600" htmlFor="email">
                  Email
                </label>
                <input
                  className="w-full px-5 py-1 text-gray-700 bg-gray-200 rounded"
                  id="email"
                  name="email"
                  type="email"
                  required=""
                  aria-label="Email"
                  value={email}
                  onChange={handleInputChange}
                />
              </div>
              <div className="mt-2">
                <label className=" block text-sm text-gray-600" htmlFor="phone">
                  Telefono
                </label>
                <input
                  className="w-full px-2 py-2 text-gray-700 bg-gray-200 rounded"
                  id="phone"
                  name="phone"
                  type="text"
                  required=""
                  value={phone}
                  onChange={handleInputChange}
                />
              </div>
              <div className="mt-5">
                <label
                  className="block text-sm text-gray-600"
                  htmlFor="address"
                >
                  Direccin
                </label>
                <textarea
                  className="w-full px-2 py-2 text-gray-700 bg-gray-200 rounded"
                  rows="3"
                  value={address}
                  onChange={handleInputChange}
                  id="address"
                  name="address"
                />
              </div>

              <div className="bg-gray-50 px-4 py-3 mt-5 sm:px-6 sm:flex sm:flex-row-reverse">
                <button
                  type="button"
                  className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-blue-400 text-base font-medium text-white hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
                  onClick={() => createClient()}
                  disabled={
                    name.trim().length === 0 ||
                    email.trim().length === 0 ||
                    phone.trim().length === 0
                  }
                >
                  Agregar
                </button>
                <button
                  type="button"
                  className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm"
                  onClick={() => closeModal(false)}
                >
                  Cancelar
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
