import { useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from "react-router-dom";
import { Client } from "../pages/Client";
import { Dashboard } from "../pages/Dashboard";
import { Service } from "../pages/Service";
import { PrivateRoute } from "./PrivateRouter";
import { PublicRoute } from "./PublicRouter";
import "@fortawesome/fontawesome-free/css/all.min.css";
import "../assets/tailwind.css";
import Login from "../pages/Login";
import { useDispatch } from "react-redux";
import { login } from "../actions/auth";
import { setClient, startLoadingClient } from "../actions/client";
import NotFoundPage from "../components/NotFound/NotFoundPage";
import { setService, startLoadingService } from "../actions/service";
import WorkShop from "../pages/WorkShop";
import { setWorkShop, startLoadingWorkShop } from "../actions/workShop";
import { Quotation } from "../pages/Quotation";
import FormAdd from "../components/Quotations/FormAdd";
import { setquotation, startLoadingQuotation } from "../actions/quotation";
import Details from "../components/Quotations/Details";
import Swal from "sweetalert2";
import Loading from "../components/Loader/Loading";

export default function AppRouter() {
  const dispatch = useDispatch();

  const [checking, setChecking] = useState(true);
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const getAuth = async () => {
      const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
      if (userInfo === null) {
        setIsLoggedIn(false);
      } else {
        dispatch(login(userInfo.token, userInfo.name, userInfo.image));
        const dataClient = await startLoadingClient(1);
        const dataService = await startLoadingService(1);
        const dataWorkShop = await startLoadingWorkShop();
        const dataQuotation = await startLoadingQuotation(1);
        if (
          dataClient.status &&
          dataService.status &&
          dataWorkShop.status &&
          dataQuotation.status
        ) {
          dispatch(setClient(dataClient.clients));
          dispatch(setService(dataService.services));
          dispatch(setWorkShop(dataWorkShop.workshop));
          dispatch(setquotation(dataQuotation.quotations));
        } else {
          Swal.fire(
            "Error",
            "Problemas con el servidor, intntelo ms tarde...",
            "error"
          );
        }
        setIsLoggedIn(true);
      }
      setChecking(false);
    };
    getAuth();
  }, [dispatch, setChecking, setIsLoggedIn]);

  if (checking) {
    return <Loading />;
  }

  return (
    <Router>
      <Switch>
        <PublicRoute
          exact
          path="/auth/login"
          isAuthenticated={isLoggedIn}
          component={Login}
        />

        <PrivateRoute
          exact
          path="/client"
          isAuthenticated={isLoggedIn}
          component={Client}
        />
        <PrivateRoute
          exact
          path="/service"
          isAuthenticated={isLoggedIn}
          component={Service}
        />

        <PrivateRoute
          exact
          path="/workshop"
          isAuthenticated={isLoggedIn}
          component={WorkShop}
        />

        <PrivateRoute
          exact
          path="/quotation"
          isAuthenticated={isLoggedIn}
          component={Quotation}
        />

        <PrivateRoute
          exact
          path="/addquotation"
          isAuthenticated={isLoggedIn}
          component={FormAdd}
        />

        <PrivateRoute
          exact
          path="/details"
          isAuthenticated={isLoggedIn}
          component={Details}
        />

        <PrivateRoute
          exact
          path="/"
          isAuthenticated={isLoggedIn}
          component={Dashboard}
        />
        <Route path="/404" component={NotFoundPage} />
        <Route path="*">
          <Redirect to="/404" />
        </Route>
      </Switch>
    </Router>
  );
}
