import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import Swal from "sweetalert2";
import {
  setquotation,
  startLoadingQuotation,
  startNewQuotation,
} from "../../actions/quotation";
import { useForm } from "../../hooks/useForm";
import Loading from "../Loader/Loading";
import TableServiceAdd from "./TableServiceAdd";

export default function FormAdd() {
  const dispatch = useDispatch();
  const [checking, setChecking] = useState(false);
  const { clients } = useSelector((state) => state.client);
  const [showModal, setShowModal] = useState(false);
  var [services, setServices] = useState([]);
  const [totalService, setTotal] = useState(0);
  let history = useHistory();

  var [formValues, handleInputChange] = useForm({
    clientId: "",
  });
  var { clientId } = formValues;

  const addService = (service) => {
    const result = services.find(function (item) {
      return item.id === service.id;
    });
    if (result === undefined) {
      services.push(service);
      setServices(services);
      loadingTotal();
    }
    setShowModal(false);
  };

  const backToPage = () => {
    history.push("/quotation");
  };

  const createQuotation = async () => {
    setChecking(true);
    if (services.length === 0 || clientId.trim().length === 0) {
      Swal.fire("Error", "Ingresa todo los campos", "error");
      setChecking(false);
      return false;
    }
    const data = await startNewQuotation(clientId, services, totalService);
    if (data.status) {
      const data = await startLoadingQuotation(1);
      if (data.status) {
        dispatch(setquotation(data.quotations));
      }
      history.push("/quotation");
      setChecking(false);
    } else {
      setChecking(false);
      Swal.fire("Error", "Error del servidor", "error");
    }
  };

  const loadingTotal = () => {
    let totalServices = 0;
    services.forEach((item) => {
      totalServices = totalServices + item.price;
    });
    setTotal(totalServices);
  };

  const deleteService = (id) => {
    services = services.filter((service) => service.id !== id);
    setServices(services);
    loadingTotal();
  };

  if (checking) {
    return <Loading />;
  }

  return (
    <div className="bg-gray-200 min-h-screen pt-2 font-mono my-16">
      <div className="container mx-auto">
        <div className="inputs w-full max-w-2xl p-6 mx-auto">
          <h2 className="text-2xl text-gray-900">Nueva Cotizacin</h2>
          <div className="mt-6 border-t border-gray-400 pt-4">
            <div className="flex flex-wrap -mx-3 mb-6">
              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Selecciona el cliente
                </label>
                <div className="flex-shrink w-full inline-block relative">
                  <select
                    className="block appearance-none text-gray-600 w-full bg-white border border-gray-400 shadow-inner px-4 py-2 pr-8 rounded"
                    id="clientId"
                    name="clientId"
                    value={clientId}
                    onChange={handleInputChange}
                  >
                    <option>Seleccionar ...</option>
                    {clients.map((client) => (
                      <option key={client.id} value={client.id}>
                        {client.name} - {client.phone}
                      </option>
                    ))}
                  </select>
                  <div className="pointer-events-none absolute top-0 mt-3  right-0 flex items-center px-2 text-gray-600">
                    <svg
                      className="fill-current h-4 w-4"
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 20 20"
                    >
                      <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />
                    </svg>
                  </div>
                </div>
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <div className="flex">
                  <div className="w-1/2">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                      Lista de servicios
                    </label>
                  </div>
                  <div className="w-1/2">
                    <button
                      className="rounded-r-none  hover:scale-110 focus:outline-none rounded cursor-pointer 
        hover:bg-teal-700 hover:text-teal-100 
        bg-teal-100 
        text-teal-700 
        border duration-200 ease-in-out 
        border-teal-600 transition"
                    >
                      <div
                        className="flex leading-5"
                        onClick={() => setShowModal(true)}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          className="feather feather-save w-5 h-5 mr-1"
                        >
                          <path d="M19 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h11l5 5v11a2 2 0 0 1-2 2z"></path>
                          <polyline points="17 21 17 13 7 13 7 21"></polyline>
                          <polyline points="7 3 7 8 15 8"></polyline>
                        </svg>
                        Agregar servicio
                      </div>
                    </button>
                  </div>
                </div>
                <div className="-mx-4 px-4 sm:px-8  overflow-x-auto">
                  <div className="inline-block min-w-full shadow rounded-lg overflow-hidden">
                    <table className="min-w-full leading-normal">
                      <thead>
                        <tr>
                          <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Nombre
                          </th>
                          <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Precio
                          </th>
                          <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Accin
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {services.map((service) => (
                          <tr key={service.id}>
                            <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                              <div className="flex items-center">
                                <div className="ml-3">
                                  <p className="text-gray-900 whitespace-normal">
                                    {service.name}
                                  </p>
                                </div>
                              </div>
                            </td>
                            <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                              <p className="text-gray-900 whitespace-normal">
                                ${service.price}
                              </p>
                            </td>
                            <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm text-left">
                              <div className="flex">
                                <div className="w-4 mr-2 transform hover:text-purple-500 hover:scale-110 cursor-pointer	">
                                  <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    fill="none"
                                    viewBox="0 0 24 24"
                                    stroke="currentColor"
                                    onClick={() => deleteService(service.id)}
                                  >
                                    <path
                                      strokeLinecap="round"
                                      strokeLinejoin="round"
                                      strokeWidth="2"
                                      d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                                    />
                                  </svg>
                                </div>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label
                  className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  htmlFor="name"
                >
                  Total: ${totalService}
                </label>
              </div>

              <div className="personal w-full border-t border-gray-400 pt-4">
                <div className="grid md:grid-cols-2 gap-2 mt-7">
                  <div>
                    <button
                      className="text-center w-full text-white bg-blue-400 p-3 duration-300 rounded-sm hover:bg-blue-700"
                      onClick={() => createQuotation()}
                    >
                      Guardar
                    </button>
                  </div>
                  <div>
                    <button
                      className="text-center w-full text-white bg-red-600 p-3 duration-300 rounded-sm hover:bg-red-800"
                      onClick={() => backToPage()}
                    >
                      Cancelar
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {showModal && (
        <TableServiceAdd closeModal={setShowModal} addService={addService} />
      )}
    </div>
  );
}
