import React, { useState } from "react";
import { useLocation } from "react-router";
import jsPDF from "jspdf";
import "jspdf-autotable";
import Logo from "../../assets/images/logo192.png";
import { useSelector } from "react-redux";
import moment from "moment";
import "moment/locale/es";
import { sendEmail } from "../../actions/quotation";
import Loading from "../Loader/Loading";

export default function Details() {
  const location = useLocation();
  const { workShops } = useSelector((state) => state.workShop);
  const [checking, setChecking] = useState(false);

  const quotation = location.state;

  console.log(quotation);
  const doc = new jsPDF();

  const sendEmailToClient = async () => {
    setChecking(true);
    doc.autoTable({
      html: "#my-table",
      didDrawPage: function (data) {
        doc.setFontSize(15);
        doc.setTextColor(40);
        doc.addImage(Logo, "PNG", data.settings.margin.left + 70, 5, 30, 20);
        doc.text("Taller El Marro", data.settings.margin.left + 70, 30);
        doc.setFontSize(10);
        doc.setTextColor(35);
        doc.text(`${workShops[0].address}`, data.settings.margin.left + 30, 35);
        doc.text(
          `Telefono: ${workShops[0].phone}`,
          data.settings.margin.left + 70,
          40
        );
        doc.setFontSize(12);
        doc.setTextColor(40);
        doc.text(
          `Cliente: ${quotation.Client.name}`,
          data.settings.margin.left,
          50
        );
        doc.setFontSize(11);
        doc.setTextColor(30);
        doc.text(
          `${moment(quotation.date).format("LL")}`,
          data.settings.margin.left + 100,
          60
        );
        doc.text(
          `${workShops[0].presentationMessage}`,
          data.settings.margin.left + 10,
          70
        );

        doc.setFontSize(10);
        doc.text(
          `Total: $${quotation.total}`,
          data.settings.margin.left + 158,
          data.cursor.y + 10
        );
        doc.setFontSize(12);
        doc.text(
          `${workShops[0].finalMessage}`,
          data.settings.margin.left + 50,
          data.cursor.y + 30
        );
        doc.text(
          `${workShops[0].sender}`,
          data.settings.margin.left + 50,
          data.cursor.y + 40
        );
      },
      margin: { top: 80 },
    });
    const uriData = doc.output("datauristring");
    await sendEmail(uriData, quotation.Client.email);
    setChecking(false);
  };

  const print = () => {
    doc.autoTable({
      html: "#my-table",
      didDrawPage: function (data) {
        doc.setFontSize(15);
        doc.setTextColor(40);
        doc.addImage(Logo, "PNG", data.settings.margin.left + 70, 5, 30, 20);
        doc.text("Taller El Marro", data.settings.margin.left + 70, 30);
        doc.setFontSize(10);
        doc.setTextColor(35);
        doc.text(`${workShops[0].address}`, data.settings.margin.left + 30, 35);
        doc.text(
          `Telefono: ${workShops[0].phone}`,
          data.settings.margin.left + 70,
          40
        );
        doc.setFontSize(12);
        doc.setTextColor(40);
        doc.text(
          `Cliente: ${quotation.Client.name}`,
          data.settings.margin.left,
          50
        );
        doc.setFontSize(11);
        doc.setTextColor(30);
        doc.text(
          `${moment(quotation.date).format("LL")}`,
          data.settings.margin.left + 100,
          60
        );
        doc.text(
          `${workShops[0].presentationMessage}`,
          data.settings.margin.left + 10,
          70
        );

        doc.setFontSize(10);
        doc.text(
          `Total: $${quotation.total}`,
          data.settings.margin.left + 158,
          data.cursor.y + 10
        );
        doc.setFontSize(12);
        doc.text(
          `${workShops[0].finalMessage}`,
          data.settings.margin.left + 50,
          data.cursor.y + 30
        );
        doc.text(
          `${workShops[0].sender}`,
          data.settings.margin.left + 50,
          data.cursor.y + 40
        );
      },
      margin: { top: 80 },
    });
    doc.save("cotizacin.pdf");
  };

  if (checking) {
    return <Loading />;
  }

  return (
    <div className="bg-gray-200 min-h-screen pt-2 font-mono my-16">
      <div className="container mx-auto">
        <div className="inputs w-full max-w-2xl p-6 mx-auto">
          <h2 className="text-2xl text-gray-900 text-center">
            Detalle Cotizacin
          </h2>
          <div className="mt-6 border-t border-gray-400 pt-4">
            <h2 className="text-1xl text-gray-900">Datos del cliente</h2>
            <br />
            <div className="flex flex-wrap -mx-3 mb-6">
              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Nombre
                </label>
                <input
                  className="bg-gray-100 rounded-md border text-gray-700 leading-normal resize-none w-full py-2 px-3 shadow-inner border border-gray-400 font-medium placeholder-gray-700 focus:outline-none focus:bg-white"
                  required
                  value={quotation.Client.name}
                  readOnly={true}
                />
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Direccin
                </label>
                <input
                  className="bg-gray-100 rounded-md border text-gray-700 leading-normal resize-none w-full py-2 px-3 shadow-inner border border-gray-400 font-medium placeholder-gray-700 focus:outline-none focus:bg-white"
                  required
                  value={quotation.Client.address}
                  readOnly={true}
                />
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Email
                </label>
                <input
                  className="bg-gray-100 rounded-md border text-gray-700 leading-normal resize-none w-full py-2 px-3 shadow-inner border border-gray-400 font-medium placeholder-gray-700 focus:outline-none focus:bg-white"
                  required
                  value={quotation.Client.email}
                  readOnly={true}
                />
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Telfono
                </label>
                <input
                  className="bg-gray-100 rounded-md border text-gray-700 leading-normal resize-none w-full py-2 px-3 shadow-inner border border-gray-400 font-medium placeholder-gray-700 focus:outline-none focus:bg-white"
                  required
                  value={quotation.Client.phone}
                  readOnly={true}
                />
              </div>

              <br />

              <div className="w-full md:w-full px-3 mb-6">
                <div className="flex">
                  <div className="w-1/2 mt-5">
                    <h2 className="text-1xl text-gray-900">
                      Lista de servicios
                    </h2>
                    <br />
                  </div>
                </div>
                <div className="-mx-4 px-4 sm:px-8  overflow-x-auto">
                  <div className="inline-block min-w-full shadow rounded-lg overflow-hidden">
                    <table id="my-table" className="min-w-full leading-normal">
                      <thead>
                        <tr>
                          <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Nombre
                          </th>
                          <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                            Precio
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {quotation.serviceList.map((service) => (
                          <tr key={service.id}>
                            <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                              <div className="flex items-center">
                                <div className="ml-3">
                                  <p className="text-gray-900 whitespace-normal">
                                    {service.name}
                                  </p>
                                </div>
                              </div>
                            </td>
                            <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                              <p className="text-gray-900 whitespace-normal">
                                ${service.price}
                              </p>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label
                  className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2 text-center"
                  htmlFor="name"
                >
                  Total: ${quotation.total}
                </label>
              </div>

              <div className="personal w-full border-t border-gray-400 pt-4">
                <div className="grid md:grid-cols-2 gap-2 mt-7">
                  <button
                    className="text-center w-full text-white bg-blue-400 p-3 duration-300 rounded-sm hover:bg-blue-700"
                    onClick={() => print()}
                  >
                    Descargar
                  </button>
                  <button
                    className="text-center w-full text-white bg-blue-700 p-3 duration-300 rounded-sm hover:bg-blue-400"
                    onClick={() => sendEmailToClient()}
                  >
                    Enviar por correo al cliente
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
