import { types } from "../types/types";
import axios from "axios";
import { clientLogout } from "./client";
import { serviceLogout } from "./service";
import { quotationLogout } from "./quotation";

export const startLogin = async (username, password) => {
  try {
    const formData = {
      username: username,
      password: password,
    };

    const res = await axios.post("v1/api/auth/signin", formData);

    const data = {
      token: res.data.token,
      username: res.data.username,
    };
    localStorage.setItem("userInfo", JSON.stringify(data));

    return {
      status: true,
      token: res.data.token,
      username: res.data.username,
    };
  } catch (err) {
    if (
      "Request failed with status code 404" === err.message ||
      "Request failed with status code 400"
    ) {
      return {
        status: false,
        message: "Usuario o contrasea incorrecta",
      };
    } else {
      return {
        status: false,
        message: "Problemas con el servidor",
      };
    }
  }
};

export const login = (token, name) => {
  return {
    type: types.login,
    payload: {
      token,
      name,
    },
  };
};

export const startLogout = () => {
  localStorage.clear();
  return async (dispatch) => {
    dispatch(clientLogout());
    dispatch(serviceLogout());
    dispatch(quotationLogout());
    dispatch(logout());
  };
};

export const logout = () => ({
  type: types.logout,
});
