import { types } from "../types/types";

const initialState = {
  workShops: [],
  active: null,
};

export const workShopReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.workShopLoad:
      return {
        ...state,
        workShops: [...action.payload],
      };

    case types.workShopUptaded:
      return {
        ...state,
        workShops: state.workShop.map((workShop) =>
          workShop.id === action.payload.id ? action.payload.workShop : workShop
        ),
      };

    case types.workShopLogoutCleaning:
      return {
        ...state,
        active: null,
        workShops: [],
      };

    default:
      return state;
  }
};
