import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import {
  setWorkShop,
  startLoadingWorkShop,
  startUpdateWorkShop,
} from "../actions/workShop";
import Loading from "../components/Loader/Loading";
import { useForm } from "../hooks/useForm";

export default function WorkShop() {
  const dispatch = useDispatch();
  const { workShops } = useSelector((state) => state.workShop);
  const [checking, setChecking] = useState(false);

  var [formValues, handleInputChange] = useForm({
    id: workShops[0].id,
    name: workShops[0].name,
    address: workShops[0].address,
    phone: workShops[0].phone,
    email: workShops[0].email,
    sender: workShops[0].sender,
    presentationMessage: workShops[0].presentationMessage,
    finalMessage: workShops[0].finalMessage,
  });
  var {
    name,
    address,
    phone,
    email,
    sender,
    presentationMessage,
    finalMessage,
  } = formValues;

  const edit = async () => {
    setChecking(true);
    if (
      name === workShops[0].name &&
      address === workShops[0].address &&
      phone === workShops[0].phone &&
      email === workShops[0].email &&
      sender === workShops[0].sender &&
      presentationMessage === workShops[0].presentationMessage &&
      finalMessage === workShops[0].finalMessage
    ) {
      setChecking(false);
      return false;
    }
    if (
      name.trim().length === 0 ||
      address.trim().length === 0 ||
      phone.trim().length === 0 ||
      email.trim().length === 0 ||
      sender.trim().length === 0 ||
      presentationMessage.trim().length === 0 ||
      finalMessage.trim().length === 0
    ) {
      Swal.fire("Error", "Ingresar todos los campos", "error");
      setChecking(false);
      return false;
    }
    await startUpdateWorkShop(
      workShops[0].id,
      name,
      address,
      phone,
      email,
      sender,
      presentationMessage,
      finalMessage
    );
    const data = await startLoadingWorkShop();
    if (data.status) {
      dispatch(setWorkShop(data.workshop));
    }
    setChecking(false);
  };

  if (checking) {
    return <Loading />;
  }

  return (
    <div className="bg-gray-200 min-h-screen pt-2 font-mono my-16">
      <div className="container mx-auto">
        <div className="inputs w-full max-w-2xl p-6 mx-auto">
          <h2 className="text-2xl text-gray-900">Ajustes</h2>
          <div className="mt-6 border-t border-gray-400 pt-4">
            <div className="flex flex-wrap -mx-3 mb-6">
              <div className="w-full md:w-full px-3 mb-6">
                <label
                  className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  htmlFor="name"
                >
                  Nombre
                </label>
                <input
                  className="appearance-none block w-full bg-white text-gray-700 border border-gray-400 shadow-inner rounded-md py-3 px-4 leading-tight focus:outline-none  focus:border-gray-500"
                  id="name"
                  name="name"
                  type="text"
                  value={name}
                  onChange={handleInputChange}
                  required
                />
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Direccin
                </label>
                <textarea
                  className="bg-gray-100 rounded-md border text-gray-700 leading-normal resize-none w-full h-20 py-2 px-3 shadow-inner border border-gray-400 font-medium placeholder-gray-700 focus:outline-none focus:bg-white"
                  required
                  id="address"
                  name="address"
                  value={address}
                  onChange={handleInputChange}
                ></textarea>
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label
                  className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  htmlFor="phone"
                >
                  Telefono
                </label>
                <input
                  className="appearance-none block w-full bg-white text-gray-700 border border-gray-400 shadow-inner rounded-md py-3 px-4 leading-tight focus:outline-none  focus:border-gray-500"
                  id="phone"
                  type="number"
                  name="phone"
                  value={phone}
                  onChange={handleInputChange}
                  required
                />
              </div>
              <div className="w-full md:w-full px-3 mb-6">
                <label
                  className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  htmlFor="email"
                >
                  Correo electronico
                </label>
                <input
                  className="appearance-none block w-full bg-white text-gray-700 border border-gray-400 shadow-inner rounded-md py-3 px-4 leading-tight focus:outline-none  focus:border-gray-500"
                  id="email"
                  type="email"
                  name="email"
                  value={email}
                  onChange={handleInputChange}
                  required
                />
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label
                  className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  htmlFor="sender"
                >
                  Remitente
                </label>
                <input
                  className="appearance-none block w-full bg-white text-gray-700 border border-gray-400 shadow-inner rounded-md py-3 px-4 leading-tight focus:outline-none  focus:border-gray-500"
                  id="sender"
                  type="text"
                  name="sender"
                  value={sender}
                  onChange={handleInputChange}
                  required
                />
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Mensaje de presentacin
                </label>
                <textarea
                  className="bg-gray-100 text-gray-700 rounded-md border leading-normal resize-none w-full h-20 py-2 px-3 shadow-inner border border-gray-400 font-medium placeholder-gray-700 focus:outline-none focus:bg-white"
                  required
                  id="presentationMessage"
                  name="presentationMessage"
                  value={presentationMessage}
                  onChange={handleInputChange}
                ></textarea>
              </div>

              <div className="w-full md:w-full px-3 mb-6">
                <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
                  Mensaje final
                </label>
                <textarea
                  className="bg-gray-100 text-gray-700 rounded-md border leading-normal resize-none w-full h-20 py-2 px-3 shadow-inner border border-gray-400 font-medium placeholder-gray-700 focus:outline-none focus:bg-white"
                  required
                  id="finalMessage"
                  name="finalMessage"
                  value={finalMessage}
                  onChange={handleInputChange}
                ></textarea>
              </div>

              <div className="personal w-full border-t border-gray-400 pt-4">
                <div className="flex justify-end">
                  <button
                    className="appearance-none bg-gray-200 text-gray-900 px-2 py-1 shadow-sm border border-gray-400 rounded-md mr-3"
                    onClick={edit}
                    disabled={
                      name.trim().length === 0 ||
                      address.trim().length === 0 ||
                      phone.trim().length === 0 ||
                      email.trim().length === 0 ||
                      sender.trim().length === 0 ||
                      presentationMessage.trim().length === 0 ||
                      finalMessage.trim().length === 0
                    }
                  >
                    Guardar Cambios
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
