import React, { useState } from "react";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import {
  deleteQuotation,
  setquotation,
  startDeleteQuotation,
  startLoadingQuotation,
} from "../../actions/quotation";
import Loading from "../Loader/Loading";
import moment from "moment";
import "moment/locale/es";

export default function TableQuotation() {
  const { quotations } = useSelector((state) => state.quotation);
  const dispatch = useDispatch();
  const [checking, setChecking] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const [currentPage, setCurrentPage] = useState(1);

  let history = useHistory();

  const redirect = () => {
    history.push("/addquotation");
  };

  const redirectDetails = (quotation) => {
    history.push({
      pathname: "/details",
      state: quotation,
    });
  };

  const handleDeleteQuotation = (id) => {
    confirmAlert({
      title: "Confirmar para eliminar",
      message: "Estas Seguro de Eliminar la cotizacin?",
      buttons: [
        {
          label: "Si",
          onClick: async () => {
            await deleteQuotationFront(id);
          },
        },
        {
          label: "No",
        },
      ],
    });
  };

  const deleteQuotationFront = async (id) => {
    setChecking(true);
    const data = await startDeleteQuotation(id);
    if (data.status) {
      dispatch(deleteQuotation(id));
      const data = await startLoadingQuotation(1);
      if (data.status) {
        dispatch(setquotation(data.quotations));
      }
    }
    setChecking(false);
  };

  const nextPage = async () => {
    const quotation = await startLoadingQuotation(currentPage + 1);
    if (quotation.quotations.length !== 0) {
      dispatch(setquotation(quotation.quotations));
      setCurrentPage(currentPage + 1);
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  };

  const previousPage = async () => {
    const quotation = await startLoadingQuotation(currentPage - 1);
    dispatch(setquotation(quotation.quotations));
    setCurrentPage(currentPage - 1);
    setDisabled(false);
  };

  const isDisabled = () => {
    if (currentPage === 1) {
      return true;
    }
    return false;
  };

  if (checking) {
    return <Loading />;
  }

  return (
    <div className="antialiased font-sans bg-gray-200">
      <div className="container mx-auto px-4 sm:px-8">
        <div className="py-8">
          <div>
            <h2 className="text-2xl font-semibold leading-tight">
              Cotizaciones
            </h2>
          </div>
          <br />
          <div className="p-5">
            <div className="flex justify-around items-baseline flex-wrap">
              <div className="flex m-2">
                <button
                  className="text-base  rounded-r-none  hover:scale-110 focus:outline-none flex justify-center px-4 py-2 rounded font-bold cursor-pointer 
        hover:bg-teal-700 hover:text-teal-100 
        bg-teal-100 
        text-teal-700 
        border duration-200 ease-in-out 
        border-teal-600 transition"
                >
                  <div className="flex leading-5" onClick={() => redirect()}>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="100%"
                      height="100%"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      className="feather feather-save w-5 h-5 mr-1"
                    >
                      <path d="M19 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h11l5 5v11a2 2 0 0 1-2 2z"></path>
                      <polyline points="17 21 17 13 7 13 7 21"></polyline>
                      <polyline points="7 3 7 8 15 8"></polyline>
                    </svg>
                    Nueva Cotizacin
                  </div>
                </button>
              </div>
            </div>
          </div>

          <div className="-mx-4 px-4 sm:px-8 py-4 overflow-x-auto">
            <div className="inline-block min-w-full shadow rounded-lg overflow-hidden">
              <table className="min-w-full leading-normal">
                <thead>
                  <tr>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Cliente
                    </th>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Descripcin
                    </th>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Total
                    </th>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Fecha
                    </th>
                    <th className="px-5 py-3 border-b-2 border-gray-200 bg-gray-100 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                      Acciones
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {quotations.map((quotation) => (
                    <tr key={quotation.id}>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                        <div className="flex items-center">
                          <div className="ml-3">
                            <p className="text-gray-900 whitespace-no-wrap">
                              {quotation.Client.name}
                            </p>
                          </div>
                        </div>
                      </td>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                        <p className="text-gray-900 whitespace-normal">
                          {quotation.description}
                        </p>
                      </td>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                        <p className="text-gray-900 whitespace-no-wrap">
                          {quotation.total}
                        </p>
                      </td>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm">
                        <p className="text-gray-900 whitespace-no-wrap">
                          {moment(quotation.date).format("LL")}
                        </p>
                      </td>
                      <td className="px-5 py-5 border-b border-gray-200 bg-white text-sm text-left">
                        <div className="flex">
                          <div className="w-5 mr-2 transform text-blue-500 hover:text-blue-900 hover:scale-110 cursor-pointer">
                            <svg
                              className="svg-icon"
                              viewBox="0 0 20 20"
                              onClick={() => redirectDetails(quotation)}
                            >
                              <path d="M15.396,2.292H4.604c-0.212,0-0.385,0.174-0.385,0.386v14.646c0,0.212,0.173,0.385,0.385,0.385h10.792c0.211,0,0.385-0.173,0.385-0.385V2.677C15.781,2.465,15.607,2.292,15.396,2.292 M15.01,16.938H4.99v-2.698h1.609c0.156,0.449,0.586,0.771,1.089,0.771c0.638,0,1.156-0.519,1.156-1.156s-0.519-1.156-1.156-1.156c-0.503,0-0.933,0.321-1.089,0.771H4.99v-3.083h1.609c0.156,0.449,0.586,0.771,1.089,0.771c0.638,0,1.156-0.518,1.156-1.156c0-0.638-0.519-1.156-1.156-1.156c-0.503,0-0.933,0.322-1.089,0.771H4.99V6.531h1.609C6.755,6.98,7.185,7.302,7.688,7.302c0.638,0,1.156-0.519,1.156-1.156c0-0.638-0.519-1.156-1.156-1.156c-0.503,0-0.933,0.322-1.089,0.771H4.99V3.062h10.02V16.938z M7.302,13.854c0-0.212,0.173-0.386,0.385-0.386s0.385,0.174,0.385,0.386s-0.173,0.385-0.385,0.385S7.302,14.066,7.302,13.854 M7.302,10c0-0.212,0.173-0.385,0.385-0.385S8.073,9.788,8.073,10s-0.173,0.385-0.385,0.385S7.302,10.212,7.302,10 M7.302,6.146c0-0.212,0.173-0.386,0.385-0.386s0.385,0.174,0.385,0.386S7.899,6.531,7.688,6.531S7.302,6.358,7.302,6.146"></path>
                            </svg>
                          </div>
                          <div className="w-5 mr-2 transform text-red-600 hover:text-red-900 hover:scale-110 cursor-pointer">
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                              onClick={() =>
                                handleDeleteQuotation(quotation.id)
                              }
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth="2"
                                d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                              />
                            </svg>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <div className="px-5 py-5 bg-white border-t flex flex-col xs:flex-row items-center xs:justify-between          ">
                <div className="inline-flex mt-2 xs:mt-0">
                  <button
                    onClick={previousPage}
                    disabled={isDisabled()}
                    className={
                      isDisabled()
                        ? "text-sm bg-gray-300 hover:bg-red-400 text-gray-800 font-semibold py-2 px-4 rounded-l"
                        : "text-sm bg-blue-300 hover:bg-blue-400 text-gray-800 font-semibold py-2 px-4 rounded-l"
                    }
                  >
                    Anterior
                  </button>
                  <button
                    onClick={nextPage}
                    disabled={disabled}
                    className={
                      disabled
                        ? "text-sm bg-gray-300 hover:bg-red-400 text-gray-800 font-semibold py-2 px-4 rounded-r"
                        : "text-sm bg-blue-300 hover:bg-blue-400 text-gray-800 font-semibold py-2 px-4 rounded-r"
                    }
                  >
                    Siguiente
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
