import { types } from "../types/types";

const initialState = {
  clients: [],
  active: null,
};

export const clientReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.clientActive:
      return {
        ...state,
        active: {
          ...action.payload,
        },
      };

    case types.clientAddNew:
      return {
        ...state,
        clients: [action.payload, ...state.clients],
      };

    case types.clientLoad:
      return {
        ...state,
        clients: [...action.payload],
      };

    case types.clientUpdated:
      return {
        ...state,
        clients: state.clients.map((client) =>
          client.id === action.payload.id ? action.payload.client : client
        ),
      };

    case types.clientDelete:
      return {
        ...state,
        active: null,
        clients: state.clients.filter((client) => client.id !== action.payload),
      };

    case types.clientLogoutCleaning:
      return {
        ...state,
        active: null,
        clients: [],
      };

    default:
      return state;
  }
};
