import axios from "axios";
import { types } from "../types/types";
import { startLogout } from "./auth";

const url = "v1/api";

export const startLoadingWorkShop = async () => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "Application/json",
      authorization: userInfo.token,
    };

    const res = await axios.get(`${url}/workshop?pageNum=${1}`, {
      headers,
    });

    const workshop = res.data;

    return {
      status: true,
      workshop: workshop,
    };
  } catch (err) {
    if (err.response) {
      if (err.response.data.message === "Invalid token") {
        startLogout();
        return {
          status: false,
          message: "Inicie sesin nuevamente",
        };
      }
      console.log(err);
      return {
        status: false,
      };
    } else {
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const startUpdateWorkShop = async (
  id,
  name,
  address,
  phone,
  email,
  sender,
  presentationMessage,
  finalMessage
) => {
  try {
    const userInfo = await JSON.parse(localStorage.getItem("userInfo"));
    const headers = {
      "Content-Type": "application/json",
      authorization: userInfo.token,
    };

    const formData = {
      name: name,
      address: address,
      phone: phone,
      email: email,
      sender: sender,
      presentationMessage: presentationMessage,
      finalMessage: finalMessage,
    };

    await axios.patch(`${url}/workshop/${id}`, formData, {
      headers,
    });
    return {
      status: true,
    };
  } catch (err) {
    if (err.response.data.message === "Invalid token") {
      startLogout();
      return {
        status: false,
        message: "Inicie sesin nuevamente",
      };
    } else {
      console.log(err.request);
      return {
        status: false,
        message: "Error de servidor",
      };
    }
  }
};

export const setWorkShop = (workshop) => ({
  type: types.workShopLoad,
  payload: workshop,
});

export const workShopLogout = () => ({
  type: types.workShopLogoutCleaning,
});
